import { Box, Card, Typography } from '@material-ui/core'
import CenterBox from 'component/center-box'
import { Icon, IconProps } from 'component/icons'
import React from 'react'
import { useStyles } from './story-card.style'

export type StoryCardProps = {
	iconImg: IconProps['imgPath']
	title: string
	mainImg: string
	bottomImg: string
}
const StoryCard = (p: StoryCardProps) => {
	const styles = useStyles()
	return (
		<CenterBox>
			<Card className={styles.storyCard}>
				<CenterBox mt={4} mb={2} className={styles.iconContainer}>
					<Icon imgPath={p.iconImg} />
				</CenterBox>
				<Typography align='center' variant='h5'>
					{p.title}
				</Typography>
				<Box>
					<img src={p.mainImg} alt='a story' />
					<Typography variant='body1'>{p.title}</Typography>
					<Box className={styles.storyCardBottom}>
						<img src={p.bottomImg} alt='background' />
					</Box>
				</Box>
			</Card>
		</CenterBox>
	)
}
export default StoryCard
