import React from 'react'
import { StyleProps, useStyles } from './index.style'

export type BgContainerProps = {
	header: JSX.Element | JSX.Element[]
	main: JSX.Element | JSX.Element[]
} & { styleOptions?: StyleProps }

const BgContainer = (p: BgContainerProps) => {
	const styles = useStyles(p.styleOptions)
	return (
		<div className={styles.container}>
			<header className={styles.header}>{p.header}</header>
			<main>{p.main}</main>
		</div>
	)
}
export default BgContainer
